@import '../../../asset/style/variable';

.title {
  display: inline-block;
  margin: 0 8px;
  font-weight: $font-weight-md;
  text-decoration: unset;
  color: $snow;
  &:hover {
    color: $blue;
  }
}

.button {
  border: none;
  font-size: $font-size-md;
  background: transparent;
  color: $snow;
  &:hover {
    color: $blue;
  }
}

.check {
  color: $green;
}
