$snow: #e8eced;
$near-black: #0e1117;
$shadow: #1a2430;
$raven: #141d27;
$stone: #818f99;
$blue: #3eb8ff;
$green: #32b877;
$red: #c71a2f;

$input-border: 1px solid $stone;
$input-focus-border: 1px solid $snow;
$border-radius: 4px;
$box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);

$font-weight-sm: 400;
$font-weight-md: 500;
$font-weight-lg: 600;
$font-weight-xl: 700;

$font-size-xs: 10px;
$font-size-sm: 12px;
$font-size-md: 14px;
