@import '../../asset/style/variable';

.container {
  border-radius: $border-radius;
  background: $shadow;
  box-shadow: $box-shadow;
}

.search {
  font-weight: $font-weight-md;
  font-size: $font-size-md;
  color: $snow;
  background: $raven;
  border: $input-border;
  border-radius: $border-radius;
  &:focus {
    color: $snow;
    background: $raven;
    border: $input-focus-border;
    box-shadow: none;
  }
}
